@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Montserrat", sans-serif;
}

html::-webkit-scrollbar {
    width: 0.5rem;
}

html::-webkit-scrollbar-track {
    background: #333;
}

html::-webkit-scrollbar-thumb {
    background: lightskyblue;
}
.destination-container {
    position: relative;
    top: 9rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0 auto;
    width: 1124px;
}

.destination-left-container {
    width: 40%;
    margin: 0 auto;
}

.destination-right-container {
    width: 60%;
    margin: 0 auto;
}

.destination-right-container img {
    width: 100%;
}

.input-container input {
    width: 90%;
}

.search-btn {
    background-color: tomato;
    color: white;
    padding: 18px 20px;
    margin-top: 10px;
    margin-bottom: 20px;
    width: 90%;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    font-size: 15px;
}

.search-btn:hover {
    color: black;
    background-color: rgb(255, 132, 110);
    transition: 0.5s ease;
}

.search-result {
    background-color: tomato;
    color: white;
    text-align: start;
    font-size: 25px;
    padding: 18px 20px;
    margin-top: 10px;
    margin-bottom: 20px;
    width: 90%;
    border-radius: 10px;
    border: none;
    font-weight: bold;
}

.li {
    border-left: 1px solid white;
    margin: 10px 0px;
}

.search-info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.name,
.price,
.count {
    padding: 15px 15px;
    font-weight: bold;
    font-size: 20px;
}

.name {
    padding-right: 50px;
}

.price,
.name {
    padding-left: 35px;
}

.search-image {
    margin: 0px 40px 0px 0px;
    width: 13%;
}

.search-user-image {
    margin: 0 0;
    width: 6%;
    height: 6%;
    padding: 15px 0px;
}

@media (max-width: 1024px) {
    .destination-container {
        position: relative;
        top: 12rem;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        margin: 0 auto;
        width: 924px;
    }
    .search-result {
        width: 90%;
    }
    .search-info {
        width: 100%;
        margin: 0 auto;
    }
    .name,
    .price,
    .count {
        padding: 15px 15px;
        font-weight: bold;
        font-size: 20px;
    }
}

@media (max-width: 960px) {
    .destination-container {
        position: relative;
        top: 12rem;
        display: inline;
        margin: 0 auto;
        width: 1124px;
    }
    .destination-left-container {
        width: 90%;
        margin: 10px auto;
    }
    .destination-right-container {
        width: 90%;
        margin: 70px auto;
    }
    .input-container input {
        width: 100%;
    }
    .search-result {
        width: 100%;
    }
    .search-info {
        width: 100%;
        margin: 0 auto;
    }
}
/* Laptop or Computer Device */

.map-container {
    width: 100%;
    height: 68vh;
    position: relative;
}


/* Tab Device */

@media (max-width: 1024px) {
    .map-container {
        width: 100%;
        height: 50vh;
        position: relative;
    }
}


/* Mobile Device */

@media (max-width: 768px) {
    .map-container {
        width: 100%;
        height: 50vh;
        position: relative;
    }
}
.main-section {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    position: relative;
    top: 15rem;
    cursor: pointer;
}

.card {
    width: 240px;
    height: 250px;
    margin: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background: #fff;
    box-sizing: border-box;
}

.card-image {
    margin: 40px auto;
    width: 60%;
    display: block;
}

.card-name {
    text-align: center;
    font-weight: bolder;
    font-size: 25px;
    text-transform: uppercase;
    text-decoration: none;
}
.banner {
    width: 100%;
    height: 115vh;
    background: url(/druta-riders/static/media/Bg.250bcbb7.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

@media (max-width: 1024px) {
    .banner {
        width: 100%;
        height: 122vh;
    }
}

@media (max-width: 960px) {
    .banner {
        width: 100%;
        height: 242vh;
    }
}
.login-container {
    position: relative;
    top: 5rem;
}

.social-login img {
    width: 24px;
}

a {
    text-decoration: none;
}

.card-login {
    font-family: sans-serif;
    width: 500px;
    margin: 3rem auto;
    border-radius: 5px;
    border: 1px solid rgba(167, 167, 167, 0.8);
    background-color: #ffff;
    padding: 1.8rem;
    box-shadow: 2px 5px 20px rgba(0, 0, 0, 0.1);
}

.title {
    text-align: start;
    font-weight: bold;
    margin: 0rem 0rem 3rem 0rem;
}

.subtitle {
    text-align: center;
    font-weight: 600;
    font-size: 15px;
}

.btn-text {
    margin: 0;
    cursor: pointer;
}

.social-login {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    grid-gap: 5px;
    gap: 5px;
    width: 400px;
    margin: 10px auto;
}

.google-btn {
    background: #fff;
    border: 1px solid rgba(167, 167, 167, 0.8);
    border-radius: 8px;
    font-weight: bold;
    display: -webkit-flex;
    display: flex;
    padding: 10px 10px;
    -webkit-flex: auto;
            flex: auto;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 5px;
    gap: 5px;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 3rem;
}

.or {
    text-align: center;
    font-weight: bold;
    border-bottom: 1px solid rgba(167, 167, 167, 0.8);
    line-height: 0.1em;
    margin: 25px auto;
    width: 400px;
}

.or span {
    background: #fff;
    padding: 0 10px;
}

.email-login {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.email-login label {
    color: rgb(170 166 166);
}

input[type="text"],
input[type="password"] {
    padding: 15px 20px;
    margin-top: 8px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-sizing: border-box;
}

.cta-btn {
    background-color: tomato;
    color: white;
    padding: 18px 20px;
    margin-top: 10px;
    margin-bottom: 20px;
    width: 100%;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    font-size: 15px;
}

.cta-btn:hover {
    color: black;
    background-color: rgb(255, 132, 110);
    transition: 0.5s ease;
}

.forget-pass {
    text-align: center;
    display: block;
}

.p {
    cursor: pointer;
    color: tomato;
}

.span {
    color: red;
}

@media (max-width: 1024px) {
    .login-container {
        top: 10rem;
    }
}

@media (max-width: 960px) {
    .login-container {
        top: 9rem;
        width: 100%;
        margin: 0 auto;
    }
    .card-login {
        width: 90%;
        margin: 3rem auto;
    }
    .social-login {
        width: 90%;
        margin: 10px auto;
    }
    .or {
        margin: 25px auto;
        width: 80%;
    }
    .subtitle {
        font-size: 13px;
    }
}
.navbar {
    position: absolute;
    top: 15px;
    left: 0;
    padding: 0;
    width: 100%;
}

.navbar__text {
    display: -webkit-flex;
    display: flex;
    height: 60px;
    -webkit-align-items: center;
            align-items: center;
    width: 1124px;
    margin: 0 auto;
    border-bottom: 1px solid black;
}

.logo {
    width: 50%;
}

.logo img {
    padding-top: 10px;
    width: 220px;
}

.navbar__ul {
    width: 60%;
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.nav-menu li a {
    text-decoration: none;
    color: black;
    padding: 0 15px;
    font-weight: 800;
    text-transform: capitalize;
}

.nav-menu {
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    -webkit-justify-content: end;
            justify-content: end;
    margin-left: 2rem;
    width: 60%;
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.nav-item {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
}

.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    color: tomato;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.fa-bars,
.fa-times {
    color: black;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

.btn-login {
    background-color: tomato;
    padding: 10px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    color: white;
    cursor: pointer;
}

.btn-login:hover {
    color: black;
    background-color: rgb(228, 228, 228);
    transition: 0.5s ease;
}

.user-name-nav {
    color: tomato;
    font-size: 16px;
}

@media (max-width: 1024px) {
    .navbar {
        width: 100%;
    }
    .navbar__text {
        display: -webkit-flex;
        display: flex;
        height: auto;
        -webkit-align-items: center;
                align-items: center;
        width: 924px;
        margin: 0 auto;
    }
}

@media (max-width: 960px) {
    .navbar {
        width: 100%;
    }
    .navbar__text {
        display: -webkit-flex;
        display: flex;
        height: auto;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
        width: 100%;
        margin: 0 auto;
    }
    .logo {
        margin: 20px auto;
        width: 90%;
        display: block;
    }
    .NavbarItems {
        position: relative;
    }
    .nav-menu {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 60vh;
        position: absolute;
        top: 118px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
        margin: 0 0;
        z-index: 1;
    }
    .nav-menu.active {
        background: #555555;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }
    .nav-links,
    .btn-login {
        text-align: center;
        width: 100%;
        display: table;
    }
    .user-name-nav {
        margin: 0 auto;
        width: 100%;
        padding: 0rem auto;
        left: 0%;
        position: absolute;
    }
    .nav-links:hover {
        border-radius: 0;
    }
    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 50%);
                transform: translate(25%, 50%);
    }
    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        cursor: pointer;
    }
    .fa-chevron-down {
        display: none;
    }
    .fa-bars,
    .fa-times {
        border: 2px solid black;
        padding: 2px 5px;
        font-size: 25px;
        margin-left: -1.5rem;
        margin-top: 0.6rem;
    }
    .menu h1 {
        font-size: 25px;
    }
}
